@import 'style/common/var';

.container {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  margin-left: 20px;
  width: 335px;
}

@media screen and (max-width: 375px) {
  .container {
    width: 100%;
    margin-left: 0;
  }
}

@media screen and (min-width: 768px) {
  .container {
    margin-left: 64px;
    width: 470px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    margin-left: 100px;
    width: 485px;
  }
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 768px) {
  .center {
    align-items: start;
    justify-content: start;
  }
}

.wrapper {
  height: 100vh;
  background: radial-gradient(circle, rgba(64, 112, 205, 0.5) 10%, rgba(64, 112, 205, 0) 70%),
    radial-gradient(circle, rgba(188, 230, 210, 0.4) -15%, rgba(188, 230, 210, 0) 70%),
    radial-gradient(circle, rgba(64, 112, 205, 0.5) 10%, rgba(64, 112, 205, 0) 70%),
    linear-gradient(0deg, rgba(10, 10, 17, 0.2) 0%, rgba(10, 10, 17, 0.2) 100%),
    linear-gradient(1deg, #0a0a11 -0.56%, rgba(10, 10, 17, 0) 21.93%),
    linear-gradient(82deg, #0a0a11 11.89%, rgba(10, 10, 17, 0) 131.65%),
    url('../images/welcome-page/welcomePage-background_mob.jpg');
  background-color: var(--main-bg-color);
  background-repeat: no-repeat;
  background-size: 520px 550px, 300px 300px, 549px 543px, contain, contain, contain, contain;
  background-position-y: 20px, 38px, 498px, center, center, center, center;
  background-position-x: -300px, 42px, 200px, right, right, right, right;
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .wrapper {
    background-image: radial-gradient(circle, rgba(64, 112, 205, 0.5) 10%, rgba(64, 112, 205, 0) 70%),
      radial-gradient(circle, rgba(188, 230, 210, 0.4) -15%, rgba(188, 230, 210, 0) 70%),
      radial-gradient(circle, rgba(64, 112, 205, 0.5) 10%, rgba(64, 112, 205, 0) 70%),
      linear-gradient(0deg, rgba(10, 10, 17, 0.2) 0%, rgba(10, 10, 17, 0.2) 100%),
      linear-gradient(1deg, #0a0a11 -0.56%, rgba(10, 10, 17, 0) 21.93%),
      linear-gradient(82deg, #0a0a11 11.89%, rgba(10, 10, 17, 0) 131.65%),
      url('../images/welcome-page/welcomePage-background_mob@2x.jpg');
  }
}

