.title {
    color: #fafafa;
    font-feature-settings: 'liga' off;
    font-family: Manrope;
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.14;
    letter-spacing: -0.56px;
    text-align: left;
    margin-bottom: 14px;
  }
  
  @media screen and (min-width: 768px) {
    .title {
      font-size: 40px;
      line-height: 1.1;
      letter-spacing: -0.8px;
      text-align: left;
    }
  }
  